import { Controller, Block } from '@ali/4ever-cangjie';

export default function getListStyleTypeText(controller: Controller) {
  const { selection, document } = controller.value;
  const paragraph = document.getClosest(
      selection.anchor.key,
      (node) => (node as Block).type === 'paragraph',
    );
  return paragraph?.data?.list?.listStyle?.text;
}
